.navbar-box {
  position: relative;
  padding: .5rem 0 .5rem 9.3rem;
  overflow-x: hidden;
  font-size: 0;
  background: #ebebeb;
  color: #000;
}

.navbar-logo {
  position: absolute;
  left: 3.7rem;
  top: 50%;
  transform: translateY(-50%);
  width: 1.75rem;
  font-size: 0;
}

.navbar-list {
  display: inline-block;
  font-size: .15rem;
  white-space: nowrap;
}
.navbar-item {
  display: inline-block;
  position: relative;
  margin-right: 1.26rem;
  padding: .1rem 0;
  font-size: .24rem;
}
.navbar-item:first-of-type {
  color: #fe9f3a;
}
.navbar-item.active::after {
  content: '';
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  width: 0.45rem;
  height: 0.03rem;
  background: #fe9f3a;
  border-radius: .15rem;
}